.inputBlockContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  background: #F5F8FA;
  border-radius: 4px;
  font-size: 14px;
  .inputLabel {
    display: flex;
    padding: 8px 0 2px 10px;
    color: var(--dark-80);
  }
  .inputBlock {
    border: none;
    background: #F5F8FA;
    padding-left: 10px;
    height: 30px;
    font-size: 14px;
    border-bottom: 2px solid #657786;
    outline: none;

    &::placeholder {
      color: var(--dark-60);
      font-family: var(--main-font-family);
      font-size: 16px;
    }

    &:hover,
    &:focus {
      border-bottom-color: var(--blue);
    }
  }
  input.danger {
    border-bottom-color: var(--danger);
    &:hover,
    &:focus {
      border-bottom-color: var(--danger);
    }
  }
}

.message {
  width: 100%;
  height: 20px;
  margin-top: 4px;
  margin-bottom: 8px;
  color: var(--dark-80);
  display: flex;
  justify-content: space-between;
  .danger {
    color: var(--danger);
  }
}

